<div class="place-for-table">
  <div class="table-options-block">
      <TableOptions class="table-options" :name="$localizeFilter('status')" :options="statuses" :prefix="'status_'" @selectOption='selectOption'/>
  </div>
  <div class="custom-table desktop" v-if="showTable">
    <div class="custom-table-header">
      <div class="custom-table-row light header">
        <div class="custom-data-cell header">{{$localizeFilter('contract_number')}}</div>
        <div class="custom-data-cell header">{{$localizeFilter('status')}}</div>
        <div class="custom-data-cell header">{{$localizeFilter('contract_object')}}</div>
        <div class="custom-data-cell header">{{$localizeFilter('category')}}</div>
