        <div class="custom-table-row" v-for="(contract, idx) in contracts"  :key="idx" :class="idx % 2 == 0 ? 'dark': 'light'">
            <div class="custom-data-cell header">{{$localizeFilter('contract_number')}}</div>
            <div class="custom-data-cell">{{ contract.contractId }}</div>

            <div class="custom-data-cell header">{{$localizeFilter('status')}}</div>
            <div class="custom-data-cell status" :class="contract.status">{{ $localizeFilter('status_' + contract.status) }}</div>
            
            <div class="custom-data-cell header">{{$localizeFilter('contract_object')}}</div>
