            <div class="custom-data-cell header">{{$localizeFilter('next_payment_date')}}</div>
            <div class="custom-data-cell"><p>{{ $dateFilter(contract.nextAccrueInterestRateTimestamp, 'datetime') }}</p></div>

            <div class="custom-data-cell header">{{$localizeFilter('action')}}</div>
            <div class="custom-data-cell"><button class="dark-button" @click="openContractDetails(contract)">{{ $localizeFilter('open') }}</button></div>
        </div>
    </div>
  </div>
  </div>
</template>

<script>
import TableOptions from "@/components/TableOptions.vue"
export default {
  props: [
    'contracts', 'showTable'
  ],
  components: {
    TableOptions
  },
  data: () => ({
    statuses: ['all','active', 'closed'],
    status: 'all'
  }),
  methods: {
      openContractDetails(contract) {
          this.$router.push('/cabinet/contract?id='+contract.id+'&progress='+contract.percent)
      },
      selectOption(idx) {
        this.status = this.statuses[idx]
        this.$emit('changeStatus',this.status)
      }
  },
  computed: {
  }
}
</script>

