<template>
  <div class="contracts">
      <p class="title">{{$localizeFilter('my_contracts')}}</p>
       <ContractsTable 
      :showTable="contracts.length > 0"
      class="contracts-table" @changeStatus="changeStatus"
      :contracts="contracts" />
      <EmptyTable v-if="contracts.length == 0" :text="$localizeFilter('no_contracts')"/>
     
      <pagination v-model="page" :records="count" :per-page="pageSize" @paginate="pageChangeHandler"/>
  </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import ContractsTable from '@/components/ContractsTable'
import EmptyTable from '@/components/EmptyTable'
import paginationMixin from '@/mixins/pagination.mixin'

export default defineComponent({
  components: {
    ContractsTable, EmptyTable
  },
  mixins: [paginationMixin],
  watch:{
      page() {
          this.$nextTick(this.loadData);
      },
  },
  setup() {
    
  },
  data: () => ({
    contracts: [],
    count: 0,
    status: 'all'
  }),
  mounted() {
    // this.setupPagination(this.contracts)
    this.loadData()
  },
  methods: {
    //  goBack() {
    //     this.$router.back()
    //  },
    //  cancelRequest(idx) {
    //    this.requests[idx].status = 'canceled'
    //    this.$forceUpdate()
    //  },
     changeStatus(status) {
       this.status = status
       this.loadData()
     },
     async loadData() {
         try {
          const data = {status: this.status, skip: this.skip, limit: this.limit}
            this.contracts = await this.$store.dispatch('getDepositsByFilter', data)
            const {count} = await this.$store.dispatch('getDepositsByFilterCount', data)
            this.count = count
            this.setupPagination(this.contracts)
        } catch(e) {
            this.$error(e)
        } 
     }
  }
})
</script>

